body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  background-color: #333;
  color: #fff;
  padding: 20px;
}

header h1 {
  margin: 0;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin-right: 20px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
}

section {
  padding: 20px;
}

.section {
  background-color: #f9f9f9;
  margin-bottom: 20px;
  padding: 20px;
}

.button {
  display: flex;
  justify-content: center;
  padding: 10px 20px;
  background-color: #f39c12;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: #e67e22;
}

.footer {
  background-color: #333;
  color: #fff;
  padding: 20px;
  text-align: center;
}

.testimonial-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.tabhead {
  color: white;
  background: #333333;
  border: none;
  font-size:medium;
  margin-top: 0.8cm;
  margin-left: 0.5cm;
  margin-right: 0.5cm;
}
.navigation{
  display: flex;
  align-items: center;
  justify-content: center;
}
.padhere{
  padding-left: 4cm;
  padding-right: 4cm;
}
.center{
  display: flex;
  align-items: center;
  justify-content: center;
}
.fonti{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  line-height: 0.8cm;
}
.testimonial-card {
  width: 300px;
  margin: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.stars {
  margin-bottom: 10px;
}

.star {
  color: gold;
}

.review {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 10px;
}

.name {
  font-style: italic;
}
.cent {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid rgb(0, 0, 0); 
}
.myform{
  padding-left: 400px;
  margin-top: 1%;
  padding-right: 400px;
}
.myform {
  margin-bottom: 1rem;
}

.myform label {
  display: block;
  margin-bottom: 0.5rem;
}

.myform input[type="text"],
.myform input[type="email"],
.myform input[type="password"],
.myform input[type="tel"],
.myform textarea {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.myform textarea {
  height: 100px;
}

.myform button {
  padding: 0.5rem 1rem;
  background-color: #f39c12;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  
}

.myform button:hover {
  background-color: #e67e22;
}
.heading{
  display: flex;
  justify-content: center;
  color: #e67e22;
}